import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';
import linkState from 'linkstate';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
    isCapsLock: false,
    passwordType: 'password',
    password: '',
    passwordConfirm: '',
};

@observer
class UserEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadUser() {
		const { userStore } = this.props.stores;
        userStore.getInfo();
    }

    togglePassword = () => {
        let { passwordType } = this.state;
        if (passwordType === 'password') {
            passwordType = 'text';
        } else {
            passwordType = 'password';
        }
        this.setState({ passwordType });
    }

    updateCapsLockStatus = (isCapsLock) => {
        this.setState({
            isCapsLock,
        });
    }

    changePassword = async () => {
        const { password, passwordConfirm } = this.state;
        const { userStore } = this.props.stores;
        const result = await userStore.changePassword({ password, passwordConfirm });
        if (result.status === 200) {
            this.setState({
                password: '',
                passwordConfirm: '',
                passwordChanged: true,
                passwordChangeFailed: false,
            });
        } else {
            this.setState({
                passwordChanged: false,
                passwordChangeFailed: true,
            });

        }

    }

    back = () => {
        route(`/users/`, true);
    }

	componentDidMount() {
        this.loadUser();
    }

	render() {
        const { password, passwordConfirm, passwordType, isCapsLock, passwordChanged, passwordChangeFailed } = this.state;
		const { userStore } = this.props.stores;
		const { user } = userStore;
        const { email } = user;
        const passwordScore = util.scorePassword(password);
        const passwordScoreNormalized = util.normalizeBetween(passwordScore, 0, 80, 0, 100, 100);
        const passwordStrength = util.checkPassStrength(password, ['bg-danger', 'bg-warning', 'bg-success', 'bg-success']);
        const isValidPassword = passwordScoreNormalized > 30;
        const passwordsAreEqual = password === passwordConfirm;
        const isOkToChangePassword = passwordsAreEqual && isValidPassword;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <button class='btn btn-link pl-0 fixed-top'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 25px; width: 40px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4' style='padding-left: 35px;'>

                        {passwordChanged ? <div>
                            <h5><Text id='users.edit-password-changed'>Passord er endret</Text>: {email}</h5>
                            <a href='/users/'><i class='fas fa-arrow-circle-left' /><Text id='users.back-to-profile'>Tilbake til profilen</Text></a>
                        </div> : <div>
                            <h5><Text id='users.edit-password'>Endre passord</Text>: {email}</h5>

                            <div class='form-group'>
                                <label for='passwordInput'><i class='fas fa-key' /> <Text id='login.password'>Passord</Text></label>
                                <div class='input-group'>
                                    <input type={passwordType}
                                        class={`form-control`}
                                        id='passwordInput'
                                        autoComplete='new-password'
                                        onInput={linkState(this, 'password')}
                                        value={password}
                                        onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                    />
                                    <div class='input-group-append'>
                                        <button class='btn btn-outline-secondary' type='button'
                                            onClick={this.togglePassword}
                                        >
                                            <i class={`fas fa-eye${passwordType === 'text' ? '-slash' : ''}`} />
                                        </button>
                                    </div>
                                </div>
                                <div class='progress' style='height: 2px;'>
                                    <div class={`progress-bar ${passwordStrength}`} role='progressbar' style={`width: ${passwordScoreNormalized}%;`} />
                                </div>
                                <div class='invalid-feedback' style={`${passwordScoreNormalized > 0 && passwordScoreNormalized < 30 ? 'display: block' : ''}`}>
                                    <Text id='register.password-too-weak'>Passordet ditt er for svakt. Forsøk et annet passord eller legg til flere bokstaver.</Text>
                                </div>
                                <div class='valid-feedback text-warning' style={`${passwordScoreNormalized >= 30 && passwordScoreNormalized < 70 ? 'display: block' : ''}`}>
                                    <Text id='register.password-weak'>Passordet ditt er svakt, men vis skal gjøre vårt beste for å passe på deg.</Text>
                                </div>
                                <div class='valid-feedback' style={`${passwordScoreNormalized >= 70 ? 'display: block' : ''}`}>
                                    <Text id='register.password-ok'>Passordet ditt ser bra ut.</Text>
                                </div>
                            </div>
                            {isCapsLock && <div class='alert alert-danger' role='alert'>
                                <Text id='login.caps-lock-on'>CAPS LOCK er på!</Text>
                            </div>}

                            <div class='form-group'>
                                <label for='passwordInput'><i class='fas fa-key' /> <Text id='login.password-confirm'>Confirm passord</Text></label>
                                <div class='input-group'>
                                    <input type={passwordType}
                                        class={`form-control`}
                                        id='passwordInput'
                                        autoComplete='new-password'
                                        onInput={linkState(this, 'passwordConfirm')}
                                        value={passwordConfirm}
                                        onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                    />
                                    <div class='input-group-append'>
                                        <button class='btn btn-outline-secondary' type='button'
                                            onClick={this.togglePassword}
                                        >
                                            <i class={`fas fa-eye${passwordType === 'text' ? '-slash' : ''}`} />
                                        </button>
                                    </div>
                                </div>
                                <div class='valid-feedback' style={`${password && passwordsAreEqual ? 'display: block' : ''}`}>
                                    <Text id='users.passwords-matches'>Passordene er like.</Text>
                                </div>
                            </div>
                            
                            <button class={`btn btn-${isOkToChangePassword ? '' : 'outline-'}success`}
                                disabled={!isOkToChangePassword}
                                onClick={this.changePassword}
                            >
                                <Text id='users.edit-password'>Endre passord</Text>
                            </button>

                        </div>}

					</div>
				</div>
			</div>
		);
	}
}

export default UserEdit;
